import axios from "axios";
import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link } from "react-router-dom";
import { setBanner } from "../../action/BannerAction";
import AppBar from "./AppBar";
import "./user.css";

function Home() {
  const banner = useSelector((state) => state.allBanners.banData);
  const dispatch = useDispatch();

  const getAllBanner = async () => {
    const response = await axios
      .get(`http://94.237.3.78:4001/api/getBanner`)
      .catch((err) => {});
    dispatch(setBanner(response.data.bannerData));
  };

  useEffect(() => {
    getAllBanner();
  }, []);

  console.log("banner@@@@@@@@@@@@@@@@@@@@@@banner", banner);
  return (
    <div className="container-scroller">
      <AppBar />
      {banner.map((data) => (
        <div style={{ position: "absolute" }}>
          <img
            // className="imgbanner"
            style={{
              height: "27em",
              width: "87em",
            }}
            src={`http://94.237.3.78:4001/${data.banner}`}
          ></img>
          <p className="banner">{data.title}</p>
          <h3 className="bannerHeading">{data.description.slice(0, 27)}</h3>
          <h3 className="bannerHeading1">{data.description.slice(27, 80)}</h3>
          <img
            className="imglogo"
            src={`http://94.237.3.78:4001/${data.logo}`}
          ></img>
          <Link to="/register">
            <button
              type="submit"
              style={{
                background: "rgb(104, 143, 78)",
                width: "6.3em",
                border: "none",
                top: "18.2em",
                position: "absolute",
                height: "35px",
                left: "7em",
              }}
              className="btn btn-primary  "
            >
              Sign Up
            </button>
          </Link>
        </div>
      ))}
    </div>
  );
}

export default Home;
